@keyframes fromRightToLeft {
  0% {
    background-color: var(--blue);
    left: 100px;
    top: 0;
  }
  100% {
    background-color: var(--blue);
    left: 0;
    top: 0;
  }
}

@keyframes fromLeftToRight {
  0% {
    background-color: var(--blue);
    left: -100px;
    top: 0;
  }
  100% {
    background-color: var(--blue);
    left: 0;
    top: 0;
  }
}

/* #Faq .heading-section .heading-child2 .bgTest {
    position: relative;
    animation-name: fromRightToLeft;
    animation-duration: 1s;
} */

#Faq .heading-section .heading-child2 .activeOption {
  position: relative;
  animation-name: fromRightToLeft;
  animation-duration: 1s;
}

#Faq .heading-section .heading-child2 .activeOption:nth-child(2) {
  padding: 12px 16px;
  background-color: var(--blue);
  border-radius: 6000px;
  color: #fff;
  margin-right: 8px;
  font-family: "General Sans", serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  position: relative;
  animation-name: fromLeftToRight;
  animation-duration: 1s;
}

#Faq .heading-section .heading-child2 .inactiveOption {
  padding: 12px 16px;
  font-family: "General Sans", serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
}

#Faq .faq-section {
  margin-top: 75px;
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  margin-bottom: 447px;
}

.MuiAccordion-root {
  /* padding: 24px !important; */
  margin-bottom: 32px;
}

#Faq .MuiDivider-root {
  margin-bottom: 16px;
}

#Faq .heading-section .heading-child2 {
  background: #fff;
  border-radius: 30px;
  overflow: hidden;
  width: 150px;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
  position: relative;
  padding: 6px 180px 6px 6px;
  color: #000 !important;
}

#Faq .heading-section .heading-child2:before {
  content: "DeFi Specific";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
  pointer-events: none;
  /* color: #fff; */
}

#Faq .heading-section .heading-child2-checkbox {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
}

#Faq
  .heading-section
  .heading-child2-checkbox:checked
  + .heading-child2-label:before {
  transform: translateX(175px);
  transition: transform 300ms linear;
}

#Faq .heading-section .heading-child2-checkbox + .heading-child2-label {
  position: relative;
  padding: 15px 0 15px 0;
  display: block;
  user-select: none;
  pointer-events: none;
}

#Faq .heading-section .heading-child2-checkbox + .heading-child2-label:before {
  content: "";
  background-color: #1d97de;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 30px;
  transform: translateX(0);
  transition: transform 300ms;
}

#Faq
  .heading-section
  .heading-child2-checkbox
  + .heading-child2-label
  .heading-child2-label-span {
  position: relative;
  /* color: white; */
}

#Faq .faq-section .expanded-div {
  background-color: #1d97de !important;
  color: #ffffff !important;
}

@media screen and (max-width: 768px) {
  /* #Faq {
        padding: 70px 16px;
    } */
  #Faq .heading-section {
    flex-direction: column;
  }

  #Faq .faq-section {
    flex-direction: column;
  }
  #Faq .heading-section .heading-child2 {
    margin: 0 auto;
  }
}
