.d-flex {
    display: flex;
    flex-direction: row;
}

.d-flex-align-center {
    align-items: center;
}

.d-flex-justify-between {
    justify-content: space-between;
}

.primary-button {
    height: 80px;
    background: var(--dark-blue);
    border: 4px solid var(--dark-blue);
    border-radius: 40px;
    width: 100%;
    transition: 0.2s ease-in-out;
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--white);
    cursor: pointer;
}

.primary-button:hover {
    background: #243D57;
}

.secondary-button {
    height: 80px;
    border: 3px solid var(--dark-blue);
    border-radius: 40px;
    width: 100%;
    transition: 0.2s ease-in-out;
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--dark-blue);
    cursor: pointer;
    background-color: transparent;
}

.secondary-button:hover {
    background: #243D57;
    color: #FFFFFF;
}

